import React, {useCallback, useEffect, useRef, useState} from "react";
import {
  IonButton,
  IonButtons,
  IonContent,
  IonHeader,
  IonIcon,
  IonMenuButton,
  IonModal,
  IonPage,
  IonSegment,
  IonSegmentButton,
  IonTitle,
  IonToolbar,
  useIonViewWillEnter,
  useIonViewWillLeave,
} from "@ionic/react";
import {add, funnel} from "ionicons/icons";
import "../global-styles.scss";
import {getWeekStart} from "../util/time-helper";
import {addDays, startOfDay} from "date-fns";
import {AuthorizedItems} from "../orbital-interfaces/authorized-items";
import ScheduleEventsAPI from "../api/ScheduleEventsAPI";
import {DailyNotes} from "../orbital-interfaces/DailyNotes";
import {RefresherEventDetail} from "@ionic/core";
import {EventInformation, HoursList} from "../orbital-interfaces/TEC";
import {EventFilter, EventFilterView} from "../orbital-interfaces/EventFilter";
import WeekChooser from "../components/SchedulePageComponents/WeekChooser";
import EventList from "../components/SchedulePageComponents/EventList";
import ScheduleFilterModal from "../components/SchedulePageComponents/ScheduleFilterModal";
import useIonicRefresher from "../hooks/useIonicRefresher";
import EditEventModal from "../components/EditEvent/EditEventModal";
import useSimpleToast from "../hooks/useSimpleToast";
import {AuthorizedItemsContext} from "../context/AuthorizedItemsContext";
import {ApplicationUserContext} from "../context/ApplicationUserContext";
import {RouteComponentProps} from "react-router";
import Skeleton from "react-loading-skeleton";
import EventCardSkeleton from "../components/SchedulePageComponents/EventCardSkeleton";
import {EventEditAuthorization, getEventNewAuthorization,} from "../components/EditEvent/EditEventAuthorization";
import NoEventsToShow from "../components/SchedulePageComponents/NoEventsToShow";
import {useInterval} from "../hooks/useInterval";
import {getEditorValuesFromEventInformation, INITIAL_EDITOR_VAUES,} from "../components/EditEvent/EditEventModalState";
import EventGroup from "../enums/EventGroup";
import EventStatus from "../enums/EventStatus";
import {eventMatchesFilter} from "../util/scheduleevent-helper";
import {getConstants} from "../api/environment";
import authHelper from "../util/authitems-helper";
import {filterStore} from "../data/filterStore";

const initialEvents: EventInformation[] = [];
const initialDailyNotes: DailyNotes[] = [];

interface SchedulePageProps
  extends RouteComponentProps<{
    scheduleEventId?: string;
  }> {}


interface EventListRef {
  scrollToDate: (d: Date) => void;
}


// --------------------------------------------------------------------------------------
function eventPassesFilter(ev: EventInformation, filter: EventFilter): boolean {
  if (!filter.positionIds.includes(ev.PositionId)) {
    return false;
  }

  if (filter.tagId !== undefined && filter.tagId !== -1)
  {
    if (filter.tagId !== ev.TagId)
    {
      return false;
    }
  }

  return eventMatchesFilter(
    filter.eventTypeId,
    filter.eventStatusId,
    ev.EventTypeId,
    ev.EventStatusId
  )
}

// --------------------------------------------------------------------------------------
function dailyNotePassesFilter(auth: AuthorizedItems, dn: DailyNotes, filter: EventFilter): boolean {
  if (dn.DepartmentId === -1) {
    if (!filter.positionIds
      .map(pid => {return authHelper.getLocationIdFromPositionid(auth, pid)})
      .includes(dn.LocationId)) {
      return false;
    }
  } else if (dn.DepartmentId > 0) {
    if (!filter.positionIds
      .map(pid => {return authHelper.getDepartmentFromPositionId(auth, pid)?.id})
      .includes(dn.DepartmentId)) {
      return false;
    }
  }

  return true;
}

// ------------------------------------------------------------------------------------
function getAllScheduleEvents(events: EventInformation[], f: EventFilter) {
  return events.filter((ev) =>
    eventMatchesFilter(
      f.eventTypeId,
      f.eventStatusId,
      ev.EventTypeId,
      ev.EventStatusId
    )
  );
}

// ------------------------------------------------------------------------------------
function getMyScheduleEvents(
  events: EventInformation[],
  auth: AuthorizedItems,
  f: EventFilter
) {
  return events.filter(
    (ev) =>
      eventMatchesFilter(
        f.eventTypeId,
        f.eventStatusId,
        ev.EventTypeId,
        ev.EventStatusId
      ) && ev.EmployeeId === auth.employeeId
  );
}

// ------------------------------------------------------------------------------------
function getRequestScheduleEvents(events: EventInformation[]) {
  return events.filter((ev) => ev.EventStatusId === EventStatus.Request);
}

// ------------------------------------------------------------------------------------
//
// ------------------------------------------------------------------------------------
// eslint-disable-next-line react/prop-types
const SchedulePage: React.FC<SchedulePageProps> = ({ match }) => {
  const eventListRef = useRef(null as EventListRef | null);
  const { user } = React.useContext(ApplicationUserContext);
  const { auth, refreshAuth } = React.useContext(AuthorizedItemsContext);
  const toast = useSimpleToast();
  const refresher = useIonicRefresher(
    (e: CustomEvent<RefresherEventDetail>) => {
      getScheduleEvents(auth, filter, e);
    }
  );

  const startDate =
    auth && auth.locations && auth.locations.length > 0
      ? getWeekStart(
          startOfDay(new Date()),
          auth.locations[0].settings.DayWeekStartsOn,
          ""
        )
      : getWeekStart(startOfDay(new Date()), "Monday", "");

  const [myScheduleEvents, setMyScheduleEvents] = useState(initialEvents);
  const [myDailyNotes, setMyDailyNotes] = useState(initialDailyNotes);
  const [myFilteredScheduleEvents, setMyFilteredScheduleEvents] = useState(initialEvents);
  const [myFilteredDailyNotes, setMyFilteredDailyNotes] = useState(initialDailyNotes);
  const [myHoursPerDay, setMyHoursPerDay] = useState([] as HoursList[]);

  const [requestScheduleEvents, setRequestScheduleEvents] = useState(initialEvents);
  const [requestDailyNotes, setRequestDailyNotes] = useState(initialDailyNotes);
  const [requestFilteredScheduleEvents, setRequestFilteredScheduleEvents] = useState(initialEvents);
  const [requestFilteredDailyNotes, setRequestFilteredDailyNotes] = useState(initialDailyNotes);
  const [requestHoursPerDay, setRequestHoursPerDay] = useState([] as HoursList[]);

  const [allScheduleEvents, setAllScheduleEvents] = useState(initialEvents);
  const [allDailyNotes, setAllDailyNotes] = useState(initialDailyNotes);
  const [allFilteredScheduleEvents, setAllFilteredScheduleEvents] = useState(initialEvents);
  const [allFilteredDailyNotes, setAllFilteredDailyNotes] = useState(initialDailyNotes);
  const [allHoursPerDay, setAllHoursPerDay] = useState([] as HoursList[]);

  const [showLoading, setShowLoading] = useState(true);
  const [isShowing, setIsShowing] = useState(false);
  const [segment, setSegment] = useState<"my" | "requests" | "all">("my");
  const [showFilterModal, setShowFilterModal] = useState(false);
  const [showEventModal, setShowEventModal] = useState(false);
  const [editEventId, setEditEventId] = useState(null as number | null);
  const [initialEditEvent, setInitialEditEvent] = useState(INITIAL_EDITOR_VAUES);
  const [eventEditAuth, setEventEditAuth] = useState(
    null as EventEditAuthorization | null
  );

  const storedFilter = filterStore.getFilter(auth);

  const [filter, setFilter] = useState({
    tagId: storedFilter.tagId,
    positionIds: storedFilter.positionIds,
    dnLocIds: storedFilter.dnLocIds,
    settingsLocId: storedFilter.settingsLocId,
    eventTypeId: storedFilter.eventTypeId,
    eventStatusId: storedFilter.eventStatusId,
    start: startDate,
    end: addDays(startDate, 7),
    filterByEmployee: false,
    view: EventFilterView.schedule
  } as EventFilter);

  // ------------------------------------------------------------------------------------
  useIonViewWillEnter(() => {
    setIsShowing(true);
  });

  // ------------------------------------------------------------------------------------
  useIonViewWillLeave(() => {
    setIsShowing(false);
  });

  // ------------------------------------------------------------------------------------
  useInterval(
    () => {
      refreshAuth(false, user).then((newAuth) => {
        if (newAuth) {
          getScheduleEvents(newAuth, filter);
        }
      });
    },
    isShowing && !showEventModal && !showFilterModal,
    getConstants(user).refreshScheduleTimer
  );

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (user.isLoggedin && filter !== undefined) {
      setShowLoading(true);
      console.log("getScheduleEvents: from useEffect");
      getScheduleEvents(auth, filter);
    }

    // eslint-disable-next-line
  }, [user.isLoggedin]);

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    // eslint-disable-next-line react/prop-types
    if (match.params.scheduleEventId) {
      // eslint-disable-next-line react/prop-types
      setEditEventId(parseInt(match.params.scheduleEventId));
      setShowEventModal(true);
    }

    // eslint-disable-next-line react/prop-types
  }, [match.params.scheduleEventId]);

  // ------------------------------------------------------------------------------------
  const onEventClick = useCallback(
    (ev: EventInformation, newEventEditAuth: EventEditAuthorization) => {
      setEditEventId(ev.ScheduleEventId);
      setEventEditAuth(newEventEditAuth);
      setInitialEditEvent(getEditorValuesFromEventInformation(auth, ev));
      setShowEventModal(true);
    },

    // eslint-disable-next-line
    [auth.locations]
  );

  if (!authHelper.isAuthItemsValid(auth)) {
    console.log("auth items not valid yet");
    return <div />;
  }

  // ------------------------------------------------------------------------------------
  const getScheduleEvents = (
    auth: AuthorizedItems,
    f: EventFilter,
    e?: CustomEvent<RefresherEventDetail>
  ) => {
    const defaultFilter = filterStore.getDefaultFilter(auth, f);
    const apiFilter: EventFilter = {
      ...defaultFilter,
      start: f.start,
      end: f.end,
      filterByEmployee: false,
      eventTypeId: EventGroup.All,
      eventStatusId: EventGroup.All,
    };

    ScheduleEventsAPI.getScheduleEventsByFilter(user, apiFilter).then((res) => {
      setShowLoading(false);

      if (e) {
        e.detail.complete();
      }

      const mySE = getMyScheduleEvents(res.events, auth, apiFilter);
      setMyScheduleEvents(mySE);
      setMyDailyNotes(res.dailyNotes);
      setMyFilteredScheduleEvents(mySE.filter(se => eventPassesFilter(se, f)));
      const filteredDailyNotes = res.dailyNotes.filter(dn => dailyNotePassesFilter(auth, dn, f));
      setMyFilteredDailyNotes(filteredDailyNotes);
      setMyHoursPerDay(res.hoursPerDay);

      const requestSE = getRequestScheduleEvents(res.events);
      setRequestScheduleEvents(requestSE);
      setRequestDailyNotes([]);
      setRequestFilteredScheduleEvents(requestSE.filter(se => eventPassesFilter(se, {
        ...f,
        eventStatusId: EventStatus.Request,
        eventTypeId: EventGroup.All
      })));
      setRequestFilteredDailyNotes([]);
      setRequestHoursPerDay(res.hoursPerDay);

      const allSE = getAllScheduleEvents(res.events, apiFilter);
      setAllScheduleEvents(allSE);
      setAllDailyNotes(res.dailyNotes);
      setAllFilteredScheduleEvents(allSE.filter(se => eventPassesFilter(se, f)));
      setAllFilteredDailyNotes(filteredDailyNotes);
      setAllHoursPerDay(res.hoursPerDay);
    });
  };

  // ------------------------------------------------------------------------------------
  const onDismissFilterModal = (f: EventFilter, filterApplied: boolean) => {
    setFilter(f);
    if (filterApplied) {
      toast.showToast("Filter applied");
      filterStore.putFilter(f);

      setMyFilteredScheduleEvents(myScheduleEvents.filter(se => eventPassesFilter(se, f)));
      setMyFilteredDailyNotes(myDailyNotes.filter(dn => dailyNotePassesFilter(auth, dn, f)));

      setRequestFilteredScheduleEvents(requestScheduleEvents.filter(se => eventPassesFilter(se, {
        ...f,
        eventStatusId: EventStatus.Request,
        eventTypeId: EventGroup.All
        })));
      setRequestFilteredDailyNotes(requestDailyNotes.filter(dn => dailyNotePassesFilter(auth, dn, f)));

      setAllFilteredScheduleEvents(allScheduleEvents.filter(se => eventPassesFilter(se, f)));
      setAllFilteredDailyNotes(allDailyNotes.filter(dn => dailyNotePassesFilter(auth, dn, f)));

    } else {
      filterStore.clear();

      setMyFilteredScheduleEvents(myScheduleEvents);
      setMyFilteredDailyNotes(myDailyNotes);

      setRequestFilteredScheduleEvents(requestScheduleEvents);
      setRequestFilteredDailyNotes(requestDailyNotes);

      setAllFilteredScheduleEvents(allScheduleEvents);
      setAllFilteredDailyNotes(allDailyNotes);
    }

    setShowFilterModal(false);

  };

  // ------------------------------------------------------------------------------------
  const onEventUpdated = (toastMessage?: string) => {
    if (toastMessage) {
      toast.showToast(toastMessage!);
    }

    console.log("getScheduleEvents: onEventUpdated");
    getScheduleEvents(auth, filter);
    setShowEventModal(false);
  };

  // ------------------------------------------------------------------------------------
  const setStartDate = (date: Date) => {
    if (date === filter.start) {
      return;
    }

    const newFilter = {
      ...filter,
      start: date,
      end: addDays(date, 7),
    };

    setFilter(newFilter);
    setShowLoading(true);
    console.log("getScheduleEvents: setStartDate");
    getScheduleEvents(auth, newFilter);
  };

  // ------------------------------------------------------------------------------------
  const onSegmentChange = (segmentName: "my" | "requests" | "all") => {
    setSegment(segmentName);
  };

  // ------------------------------------------------------------------------------------
  const forceRefresh = (toastMessage: string) => {
    if (toastMessage && toastMessage.length > 0) {
      toast.showToast(toastMessage);
    }

    console.log("getScheduleEvents: from forceRefresh");
    getScheduleEvents(auth, filter);
  };

  const onWeekdayClick = (date: Date) => {
    eventListRef.current!.scrollToDate(date);
  }

  return (
    <IonPage id="schedule-page">
      <IonHeader className="ion-no-border">
        <IonToolbar mode="md">
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle slot="start">Schedule</IonTitle>
          <IonButtons slot="end">
            <IonButton
              onClick={() => {
                setEditEventId(null);
                setEventEditAuth(getEventNewAuthorization(auth));
                setShowEventModal(true);
              }}
            >
              <IonIcon icon={add} mode="md" slot="icon-only" />
            </IonButton>
            <IonButton onClick={() => setShowFilterModal(true)}>
              <div className={filterStore.isFilterApplied(auth, filter) ? "filter-applied" : "filter-not-applied"}>&#183;</div>
              <IonIcon icon={funnel} mode="ios" slot="icon-only" />
            </IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>

      <IonSegment mode="md" value={segment}>
        <IonSegmentButton
          mode="md"
          value="my"
          onClick={() => onSegmentChange("my")}
        >
          MY
        </IonSegmentButton>
        <IonSegmentButton
          mode="md"
          value="requests"
          onClick={() => onSegmentChange("requests")}
        >
          REQUESTS
        </IonSegmentButton>
        <IonSegmentButton
          mode="md"
          value="all"
          onClick={() => onSegmentChange("all")}
        >
          ALL
        </IonSegmentButton>
      </IonSegment>
      <WeekChooser
        startDate={getWeekStart(
          filter.start,
          auth.locations[0].settings.DayWeekStartsOn,
          ""
        )}
        setStartDate={setStartDate}
        onWeekdayClick={onWeekdayClick}
      />
      <IonContent>
        {refresher.ionicRefresherJsx}
        {!showLoading && segment === "my" && myScheduleEvents?.length === 0 && (
          <NoEventsToShow />
        )}

        {!showLoading && segment === "my" && myScheduleEvents?.length > 0 && (
          <EventList
            events={myFilteredScheduleEvents}
            listType={segment}
            notes={myFilteredDailyNotes}
            hoursPerDay={myHoursPerDay}
            auth={auth}
            onEdit={onEventClick}
            onForceRefresh={forceRefresh}
            ref={eventListRef}
            weekStart={filter.start}
          />
        )}

        {!showLoading &&
          segment === "requests" &&
          requestScheduleEvents.length === 0 && <NoEventsToShow />}

        {!showLoading &&
          segment === "requests" &&
          requestScheduleEvents.length > 0 && (
            <EventList
              events={requestFilteredScheduleEvents}
              listType={segment}
              notes={requestFilteredDailyNotes}
              hoursPerDay={requestHoursPerDay}
              auth={auth}
              onEdit={onEventClick}
              onForceRefresh={forceRefresh}
              ref={eventListRef}
              weekStart={filter.start}
            />
          )}

        {!showLoading &&
          segment === "all" &&
          allScheduleEvents?.length === 0 && <NoEventsToShow />}

        {!showLoading && segment === "all" && allScheduleEvents?.length > 0 && (
          <EventList
            events={allFilteredScheduleEvents}
            listType={segment}
            notes={allFilteredDailyNotes}
            hoursPerDay={allHoursPerDay}
            auth={auth}
            onEdit={onEventClick}
            onForceRefresh={forceRefresh}
            ref={eventListRef}
            weekStart={filter.start}
          />
        )}

        {showLoading && (
          <>
            <div style={{ marginTop: "10px" }} />
            <Skeleton height={24} width={220} />
            <br />
            <EventCardSkeleton threeRows={segment !== "my"} />
            <EventCardSkeleton threeRows={segment !== "my"} />
            <EventCardSkeleton threeRows={segment !== "my"} />
          </>
        )}
      </IonContent>

      {toast.simpleToastJsx}

      <IonModal
        isOpen={showFilterModal}
        onDidDismiss={() => setShowFilterModal(false)}
      >
        <ScheduleFilterModal
          filter={filter}
          auth={auth}
          onDismissModal={onDismissFilterModal}
          isOpen={showFilterModal}
          onCancelModal={() => setShowFilterModal(false)}
        />
      </IonModal>

      <IonModal
        isOpen={showEventModal}
        onDidDismiss={() => setShowEventModal(false)}
      >
        <EditEventModal
          isOpen={showEventModal}
          auth={auth}
          onCancelModal={() => setShowEventModal(false)}
          onDismissModal={(toastMessage) => onEventUpdated(toastMessage)}
          scheduleEventId={editEventId}
          initialEvent={initialEditEvent}
          eventEditAuth={eventEditAuth!}
        />
      </IonModal>
    </IonPage>
  );
};

export default SchedulePage;
