import { EmployeeSort } from "../orbital-interfaces/authorized-items";
import { ApplicationUser } from "../context/ApplicationUserContext";
import { formatDateTimeAsMvcString } from "../util/time-helper";
import apiHelper from "./OrbitalApiHelper";
import { IdName } from "../orbital-interfaces/IdName";
import env from "./environment";
import {ApiResult} from "./DailyNotesAPI";

export interface EmployeeCommunication {
  id: number;
  name: string;
  hasEmail: boolean;
  hasText: boolean;
  hasPush: boolean;
  isChecked: boolean;
}

// -----------------------------------------------------------------------------
const getEmployeesByLocationIdPositionIdAndActiveDateRange = async (
  user: ApplicationUser,
  locId: number,
  posId: number,
  start: Date,
  end: Date,
  sort: EmployeeSort
) => {
  const data = {
    locId,
    posId,
    start: formatDateTimeAsMvcString(start),
    end: formatDateTimeAsMvcString(end),
    sort,
  };

  return (await apiHelper.post(
    user,
    "api/v2/employees/at-position",
    data
  )) as IdName[];
};

// -----------------------------------------------------------------------------
const getEmployeeListForCommunication = async (
  user: ApplicationUser,
  locId: number,
  depId: number,
  posId: number,
  channel: number,
  role: string
) => {
  return (await apiHelper.post(
    user,
    "api/v2/employees/get-communication-list",
    {
      locId,
      depId,
      posId,
      channel,
      role,
    }
  )) as EmployeeCommunication[];
};

// -----------------------------------------------------------------------------
const submitFeedback = async (user: ApplicationUser, msg: string) => {
  const data = {
    version: env.settings.appVersionString,
    buildDate: env.settings.buildDate,
    environment: env.settings.env.item(user.environment).name,
    os: env.settings.platform,
    msg
  }

  return (await apiHelper.post(
    user,
    "/api/v2/employees/submit-feedback",
    data)) as ApiResult;
};

export default {
  getEmployeesByLocationIdPositionIdAndActiveDateRange,
  getEmployeeListForCommunication,
  submitFeedback
};
