import React, { useRef } from "react";
import { IonLabel, IonTextarea, IonCol, IonRow } from "@ionic/react";
import "../../../global-styles.scss";
import "./EventStartEndEditor";
import { AuthorizedItems } from "../../../orbital-interfaces/authorized-items";
import { EditorValues, EditorReducerAction } from "../EditEventModalState";
import { networkInterfaces } from "os";

interface EventNotesEditorProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

// --------------------------------------------------------------------------------------
const EventNotesEditor: React.FC<EventNotesEditorProps> = ({
  editorValues,
  update,
}: EventNotesEditorProps) => {
  const onNotesUpdate = (newNote?: string | null) => {
    if (!newNote) {
      newNote = "";
    }

    if (newNote !== editorValues.notes) {
      update({ type: "NOTES-UPDATED", notes: newNote });
    }
  };

  return (
    <div>
      <IonRow>
        <IonCol size="3">
          <IonLabel className="label">Notes</IonLabel>
        </IonCol>
        <IonCol size="9">
          <IonTextarea
            className="textarea"
            placeholder="Enter event note"
            maxlength={120}
            spellcheck={true}
            rows={4}
            value={editorValues.notes}
            disabled={editorValues.isReadonly}
            onIonChange={(e) => onNotesUpdate(e.detail.value)}
          />
        </IonCol>
      </IonRow>
      <div className="character-counter">{editorValues.notes.length}/120</div>
    </div>
  );
};

export default EventNotesEditor;
