
export enum EventFilterView {
  schedule = 0,
  clock = 1
}

export interface EventFilter {
  settingsLocId: number;
  dnLocIds: number[];
  start: Date;
  end: Date;
  filterByEmployee: boolean;
  positionIds: number[];
  eventTypeId: number;
  eventStatusId: number;
  tagId?: number;
  view: EventFilterView;
}

export const initialEventFilter: EventFilter = {
  settingsLocId: 0,
  dnLocIds: [],
  start: new Date(),
  end: new Date(),
  filterByEmployee: false,
  positionIds: [],
  eventTypeId: -1,
  eventStatusId: -1,
  tagId: undefined,
  view: EventFilterView.schedule
};

export interface StoredEventFilter {
  settingsLocId: number;
  dnLocIds: number[];
  positionIds: number[];
  tagId?: number;
  eventTypeId: number;
  eventStatusId: number;
}

export const initialStoredEventFilter: StoredEventFilter = {
  settingsLocId: 0,
  dnLocIds: [] as number[],
  positionIds: [] as number[],
  tagId: undefined as number | undefined,
  eventTypeId: -1,
  eventStatusId: -1
};

