import React, { useState, useEffect } from "react";
import {
  IonButton,
  IonButtons,
  IonIcon,
  IonContent,
  IonFooter,
  IonHeader,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { EventFilter } from "../../orbital-interfaces/EventFilter";
import { arrowBackOutline } from "ionicons/icons";
import "../../global-styles.scss";
import EventGroup from "../../enums/EventGroup";
import {
  getFilterByEventGroup,
  getEventTypeOptions,
} from "../../util/event-helper";
import { AuthorizedItems } from "../../orbital-interfaces/authorized-items";
import { filterStore } from "../../data/filterStore";
import authHelper from "../../util/authitems-helper";

interface ScheduleFilterModalProps {
  filter: EventFilter;
  auth: AuthorizedItems;
  onDismissModal: (f: EventFilter, filterApplied: boolean) => void;
  onCancelModal: () => void;
  isOpen: boolean;
}

// --------------------------------------------------------------------------------------
function getInitialSelectionsByFilter(
  auth: AuthorizedItems,
  filter: EventFilter
) {
  const locIds = [] as string[];
  const depIds = [] as string[];
  const posIds = [] as string[];

  if (!authHelper.isAuthItemsValid(auth)) {
    return [[] as string[], [] as string[], [] as string[]];
  }

  auth.locations.map((l) =>
    l.departments.map((d) =>
      d.positions.map((p) => {
        if (filter.positionIds.includes(p.id)) {
          const newLocId = l.id.toString();
          const newDepId = d.id.toString();
          if (!locIds.includes(newLocId)) {
            locIds.push(newLocId);
          }

          if (!depIds.includes(newDepId)) {
            depIds.push(newDepId);
          }
        }

        posIds.push(p.id.toString());

        return p.id;
      })
    )
  );

  return [locIds, depIds, posIds];
}

// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
const ScheduleFilterModal: React.FC<ScheduleFilterModalProps> = ({
  auth,
  filter,
  onDismissModal,
  onCancelModal,
  isOpen,
}: ScheduleFilterModalProps) => {
  const [
    initialLocIds,
    initialDepIds,
    initialPosIds,
  ] = getInitialSelectionsByFilter(auth, filter);
  const [myFilter, setMyFilter] = useState(filter);
  const [showCustomEventTypes, setShowCustomEventTypes] = useState(false);
  const [eventGroup, setEventGroup] = useState(EventGroup.ClockAndSched);
  const [eventTypeList, setEventTypeList] = useState(
    getEventTypeOptions(auth, filter)
  );
  const [locationIds, setLocationIds] = useState(initialLocIds);
  const [departmentIds, setDepartmentIds] = useState(initialDepIds);
  const [positionIds, setPositionIds] = useState(initialPosIds);
  const [isDirty, setIsDirty] = useState(false);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    setMyFilter(filter);
  }, [filter]);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (isOpen) {
      setIsDirty(false);
    }
  }, [isOpen]);

  // --------------------------------------------------------------------------------------
  const onReset = () => {
    setShowCustomEventTypes(false);
    setEventGroup(EventGroup.ClockAndSched);
    setEventTypeList(getEventTypeOptions(auth, filter));
    setLocationIds(auth.locations.map((l) => "" + l.id));
    setDepartmentIds(
      auth.locations.flatMap((l) => l.departments).map((d) => "" + d.id)
    );

    setPositionIds(
      auth.locations
        .flatMap((l) => l.departments)
        .flatMap((d) => d.positions)
        .map((p) => "" + p.id)
    );

    setMyFilter(filterStore.getDefaultFilter(auth, filter));
    setIsDirty(true);
  };

  // --------------------------------------------------------------------------------------
  const onEventGroupSelect = (val: EventGroup) => {
    setEventGroup(val);
    setIsDirty(true);
    setMyFilter(getFilterByEventGroup(myFilter, val));
    setShowCustomEventTypes(val === EventGroup.Custom);
  };

  // --------------------------------------------------------------------------------------
  const onSetLocations = (locIds: string[]) => {
    setLocationIds(locIds);
    setIsDirty(true);
    setDepartmentIds(
      auth.locations
        .filter((l) => locIds.indexOf(l.id.toString()) >= 0)
        .flatMap((l) => l.departments)
        .map((d) => "" + d.id)
    );
    setEventTypeList(getEventTypeOptions(auth, myFilter));
  };

  // --------------------------------------------------------------------------------------
  const onSetDepartmentIds = (deptIds: string[]) => {
    setDepartmentIds(deptIds);
    setIsDirty(true);
    setPositionIds(
      auth.locations
        .filter((l) => locationIds.indexOf(l.id.toString()) >= 0)
        .flatMap((l) => l.departments)
        .filter((d) => deptIds.includes(d.id.toString()))
        .flatMap((d) => d.positions)
        .map((p) => "" + p.id)
    );
  };

  // --------------------------------------------------------------------------------------
  const onSetPositionIds = (posIds: string[]) => {
    setPositionIds(posIds);
    setIsDirty(true);
  };

  // --------------------------------------------------------------------------------------
  const onCloseModal = () => {
    const selectedPositionIds = positionIds.map((p) => parseInt(p));
    const returnedFilter = { ...myFilter, positionIds: selectedPositionIds };
    const filterApplied = filterStore.isFilterApplied(auth, returnedFilter);
    onDismissModal(returnedFilter, filterApplied);
  };

  // --------------------------------------------------------------------------------------
  const onCancel = () => {
    onCancelModal();
  };

  return (
    <>
      <IonHeader className="ion-no-border">
        <IonToolbar>
          <IonButtons slot="start">
            <IonButton onClick={onCancel}>
              <IonIcon icon={arrowBackOutline} mode="md" slot="icon-only" />
            </IonButton>
          </IonButtons>
          <IonTitle>Filter</IonTitle>
          <IonButtons slot="end">
            <IonButton onClick={onReset}>RESET</IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>

      <IonContent class="outer-content">
        <IonItem>
          <IonLabel>Events</IonLabel>
          <IonSelect
            mode="ios"
            interface="action-sheet"
            value={eventGroup}
            onIonChange={(e) => onEventGroupSelect(e.detail.value)}
          >
            <IonSelectOption value={EventGroup.ClockAndSched}>
              Clock + Sched
            </IonSelectOption>
            <IonSelectOption value={EventGroup.Scheduled}>
              Scheduled
            </IonSelectOption>
            <IonSelectOption value={EventGroup.DontWork}>
              Don&apos;t Work
            </IonSelectOption>
            <IonSelectOption value={EventGroup.Custom}>Custom</IonSelectOption>
            <IonSelectOption value={EventGroup.All}>ALL</IonSelectOption>
          </IonSelect>
        </IonItem>

        {showCustomEventTypes && (
          <IonItem>
            <IonLabel>Type</IonLabel>
            <IonSelect
              mode="ios"
              interface="action-sheet"
              value={myFilter.eventTypeId}
              onIonChange={(e) =>
                setMyFilter({ ...myFilter, eventTypeId: e.detail.value })
              }
            >
              {eventTypeList.map((et) => (
                <IonSelectOption key={et.value} value={et.value}>
                  {et.text}
                </IonSelectOption>
              ))}
            </IonSelect>
          </IonItem>
        )}

        {showCustomEventTypes && (
          <IonItem>
            <IonLabel>Status</IonLabel>
            <IonSelect
              mode="ios"
              interface="action-sheet"
              value={myFilter.eventStatusId}
              onIonChange={(e) =>
                setMyFilter({ ...myFilter, eventStatusId: e.detail.value })
              }
            >
              <IonSelectOption value={-1}>ALL</IonSelectOption>
              <IonSelectOption value={1}>Approved</IonSelectOption>
              <IonSelectOption value={2}>Request</IonSelectOption>
              <IonSelectOption value={4}>Request Denied</IonSelectOption>
            </IonSelect>
          </IonItem>
        )}

        <IonItem>
          <IonLabel>Tags</IonLabel>
          <IonSelect
            mode="ios"
            interface="action-sheet"
            value={myFilter.tagId || -1}
            onIonChange={(e) => {
              setMyFilter({ ...myFilter, tagId: e.detail.value });
              setIsDirty(true);
            }}
          >
            <IonSelectOption key={-1} value={-1}>
              ALL
            </IonSelectOption>
            {auth.tags.map((tag) => (
              <IonSelectOption key={tag.id} value={tag.id}>
                ~{tag.name}
              </IonSelectOption>
            ))}
          </IonSelect>
        </IonItem>

        {auth.locations.length > 1 && (
          <IonItem>
            <IonLabel>Locations</IonLabel>
            <IonSelect
              mode="ios"
              multiple={true}
              onIonChange={(e) => onSetLocations(e.detail.value)}
              value={locationIds}
            >
              {auth.locations.map((loc) => (
                <IonSelectOption key={loc.id} value={loc.id.toString()}>
                  {loc.name}
                </IonSelectOption>
              ))}
            </IonSelect>
          </IonItem>
        )}

        {auth.locations.flatMap((l) => l.departments).length > 1 && (
          <IonItem>
            <IonLabel>Departments</IonLabel>
            <IonSelect
              mode="ios"
              multiple={true}
              onIonChange={(e) => onSetDepartmentIds(e.detail.value)}
              value={departmentIds}
            >
              {auth.locations
                .filter((l) => locationIds.indexOf(l.id.toString()) >= 0)
                .map((loc) =>
                  loc.departments.map((dept) => (
                    <IonSelectOption key={dept.id} value={dept.id.toString()}>
                      {loc.abbr}/{dept.name}
                    </IonSelectOption>
                  ))
                )}
            </IonSelect>
          </IonItem>
        )}

        {auth.locations
          .flatMap((l) => l.departments)
          .flatMap((d) => d.positions).length > 1 && (
          <IonItem>
            <IonLabel>Positions</IonLabel>
            <IonSelect
              mode="ios"
              multiple={true}
              onIonChange={(e) => onSetPositionIds(e.detail.value)}
              value={positionIds}
            >
              {auth.locations
                .filter((l) => locationIds.indexOf(l.id.toString()) >= 0)
                .flatMap((l) => l.departments)
                .filter((d) => departmentIds.indexOf(d.id.toString()) >= 0)
                .map((dept) =>
                  dept.positions.map((pos) => (
                    <IonSelectOption key={pos.id} value={pos.id.toString()}>
                      {pos.name}
                    </IonSelectOption>
                  ))
                )}
            </IonSelect>
          </IonItem>
        )}
      </IonContent>

      <IonFooter>
        <IonButton
          className="apply-button"
          expand="block"
          onClick={onCloseModal}
          disabled={!isDirty}
        >
          APPLY FILTER
        </IonButton>
      </IonFooter>
    </>
  );
};

export default ScheduleFilterModal;
